.vux-reddot,
.vux-reddot-border,
.vux-reddot-s {
  position: relative;
}
.vux-reddot:after,
.vux-reddot-border:after,
.vux-reddot-s:after {
  content: '';
  position: absolute;
  display: block;
  width: 8px;
  height: 8px;
  background-color: #f74c31;
  border-radius: 5px;
  right: -3px;
  top: -3px;
  background-clip: padding-box;
}
.vux-reddot-border:before {
  content: '';
  position: absolute;
  display: block;
  width: 8px;
  height: 8px;
  background-color: #fff;
  border-radius: 5px;
  right: -4px;
  top: -4px;
  background-clip: padding-box;
  padding: 1px;
}
.vux-reddot-s:after {
  width: 6px;
  height: 6px;
  top: -5px;
  right: -5px;
}
.vux-1px,
.vux-1px-t,
.vux-1px-b,
.vux-1px-tb,
.vux-1px-l,
.vux-1px-r {
  position: relative;
}
.vux-1px:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  border: 1px solid #C7C7C7;
  color: #C7C7C7;
  height: 200%;
  transform-origin: left top;
  transform: scale(0.5);
}
.vux-1px-t:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #C7C7C7;
  color: #C7C7C7;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}
.vux-1px-b:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #C7C7C7;
  color: #C7C7C7;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.vux-1px-tb:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #C7C7C7;
  color: #C7C7C7;
  transform-origin: 0 0;
  transform: scaleY(0.5);
}
.vux-1px-tb:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #C7C7C7;
  color: #C7C7C7;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.vux-1px-l:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-left: 1px solid #C7C7C7;
  color: #C7C7C7;
  transform-origin: 0 0;
  transform: scaleX(0.5);
}
.vux-1px-r:after {
  content: " ";
  position: absolute;
  right: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-right: 1px solid #C7C7C7;
  color: #C7C7C7;
  transform-origin: 100% 0;
  transform: scaleX(0.5);
}
.vux-button-group {
  -webkit-touch-callout: none;
  display: box;
  display: -ms-flexbox;
  display: flex;
}
.vux-button-group > a.vux-button-tab-item-last {
  border-top-right-radius: 32px;
  border-bottom-right-radius: 32px;
}
.vux-button-group > a.vux-button-tab-item-last:after {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  height: 1px;
  border-right: 1px solid #04BE02;
  border-top: 1px solid #04BE02;
  border-bottom: 1px solid #04BE02;
  border-left: none;
  color: #04BE02;
  height: 200%;
  transform-origin: left top;
  transform: scale(0.5);
  /* #2322 */
  z-index: 1;
  border-top-right-radius: 32px;
  border-bottom-right-radius: 32px;
}
.vux-button-group > a.vux-button-tab-item-first {
  border-top-left-radius: 32px;
  border-bottom-left-radius: 32px;
}
.vux-button-group > a.vux-button-tab-item-first:after {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  border: 1px solid #04BE02;
  color: #04BE02;
  height: 200%;
  transform-origin: left top;
  transform: scale(0.5);
  border-top-left-radius: 32px;
  border-bottom-left-radius: 32px;
}
.vux-button-group > a.vux-button-tab-item-middle:after {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  height: 1px;
  border-right: 1px solid #04BE02;
  border-top: 1px solid #04BE02;
  border-bottom: 1px solid #04BE02;
  border-left: none;
  color: #04BE02;
  height: 200%;
  transform-origin: left top;
  transform: scale(0.5);
  /* #2322 */
  z-index: 1;
}
.vux-button-group > a {
  display: block;
  position: relative;
  -ms-flex: 1;
      flex: 1;
  width: 100%;
  height: 30px;
  padding: 0;
  font-size: 14px;
  line-height: 31px;
  text-align: center;
  color: #999;
  white-space: nowrap;
  background: #fdfdfd;
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
}
.vux-button-group > a:after {
  background-clip: padding-box;
  box-sizing: border-box;
}
.vux-button-group > a.vux-button-group-current {
  color: #FFF;
  background: #04BE02;
}
.vux-button-group > a.vux-button-group-current:disabled,
.vux-button-group > a:disabled {
  border-color: #CDCDCD;
  background: #e5e5e5;
  box-shadow: 0 1px 0 rgba(255, 255, 255, .6);
  text-shadow: 0 1px 0 rgba(255, 255, 255, .8);
  color: #aaa;
}

.vux-divider {
  display: table;
  white-space: nowrap;
  height: auto;
  overflow: hidden;
  line-height: 1;
  text-align: center;
  padding: 10px 0;
  color: #666;
}
.vux-divider:after,.vux-divider:before {
  content: '';
  display: table-cell;
  position: relative;
  top: 50%;
  width: 50%;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,)
}
.vux-divider:before {
  background-position: right 1em top 50%
}
.vux-divider:after {
  background-position: left 1em top 50%
}
