  box-shadow: 0 6px 30px 0 rgba(0, 0, 0, .1);
}
.weui-skin_android .weui-dialog__title {
  font-size: 21px;
}
.weui-skin_android .weui-dialog__hd {
  text-align: left;
}
.weui-skin_android .weui-dialog__bd {
  color: #999999;
  padding: 0.25em 1.6em 2em;
  font-size: 17px;
  text-align: left;
}
.weui-skin_android .weui-dialog__bd:first-child {
  padding: 1.6em 1.6em 2em;
  color: #353535;
}
.weui-skin_android .weui-dialog__ft {
  display: block;
  text-align: right;
  line-height: 42px;
  font-size: 16px;
  padding: 0 1.6em 0.7em;
}
.weui-skin_android .weui-dialog__ft:after {
  display: none;
}
.weui-skin_android .weui-dialog__btn {
  display: inline-block;
  vertical-align: top;
  padding: 0 .8em;
}
.weui-skin_android .weui-dialog__btn:after {
  display: none;
}
.weui-skin_android .weui-dialog__btn:active {
  background-color: rgba(0, 0, 0, .06);
}
.weui-skin_android .weui-dialog__btn:visited {
  background-color: rgba(0, 0, 0, .06);
}
.weui-skin_android .weui-dialog__btn:last-child {
  margin-right: -0.8em;
}
.weui-skin_android .weui-dialog__btn_default {
  color: #808080;
}
@media screen and (min-width: 1024px) {
.weui-dialog {
    width: 35%;
}
}
.vux-prompt {
  padding-bottom: 1.6em;
}
.vux-prompt-msgbox {
  width: 80%;
  border: 1px solid #dedede;
  border-radius: 5px;
  padding: 4px 5px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  font-size: 16px;
}

.chat-fullscreen, .chat-image img {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.chat-fullscreen {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
  visibility: hidden;
  z-index: 98;
}
.chat-fullscreen.active {
    visibility: visible;
}
.chat-fullscreen.active .chat-mask {
      opacity: 0.7;
}
.chat-fullscreen.active .chat-image {
      transform: scale3d(1, 1, 1);
}
.chat-fullscreen .chat-mask, .chat-fullscreen .chat-image {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transition: all 0.5s;
}
.chat-fullscreen .chat-mask {
    left: 0;
    top: 0;
    background-color: #000;
    opacity: 0;
}
.chat-fullscreen .chat-image {
    transform: scale3d(0, 0, 0);
}
.chat-fullscreen .chat-image img {
      display: block;
      position: absolute;
      width: 100%;
      height: auto;
      /*          max-width: 100%;
        max-height: 100%;*/
}
